import { Component, Input, OnInit } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsDropdownLovComponent } from '../ps-dropdown-lov.component';
import { IOptionsPsDropdownLov } from '../ps-dropdown-lov.component.interfaces';
import { IOptionsPsLovDeliveryOfOriginalUndertakingExposed } from './ps-lov-delivery-of-original-undertaking.component.interface';

@Component({
  selector: 'ps-lov-delivery-of-original-undertaking',
  templateUrl: './ps-lov-delivery-of-original-undertaking.component.html',
  styleUrls: ['./ps-lov-delivery-of-original-undertaking.component.scss'],
})
export class PsLovDeliveryOfOriginalUndertakingComponent extends PsDropdownLovComponent implements OnInit {

  @Input() options: IOptionsPsLovDeliveryOfOriginalUndertakingExposed;

  defaultSelectOptions: IOptionsPsDropdownLov = {
    lovTypeId: CommonBussinessConstant.DELIVERY_OF_ORGINAL_UNDERTAKING_LOV_TYPE_ID
  };
  constructor(commonProvService: PsCommonService, logger: LoggerService) {
    super(commonProvService, logger);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultSelectOptions, this.options, false);
    this.defaultSelectOptions.labelKey = this.commonProv.translate('delivery_of_orginal_undertaking');
    this.defaultSelectOptions.placeHolder = this.commonProv.translate('select_delivery_of_orginal_undertaking');
  }
  onChangeLov(values: IchangeValues) {
    this.onPsChange.emit(values);
  }

}



